import React from 'react';
import { Link } from 'react-router-dom';
import './css/about.css';

// import Button from 'antd/lib/button';
import {
    Button,
    Input,
    Layout,
    Card
} from 'antd';

const InputGroup = Input.Group;
const { Header, Footer, Sider, Content } = Layout;

class AboutPanel extends React.Component {

    render() {
        return (
            <div className='about-panel'>
                <h3 className='about-title'>{this.props.title}</h3>
                {this.props.subTitle}
                <p>{this.props.desc}</p>

                {this.props.children}
            </div>
        );
    }

}

class AboutPanelSpecial extends React.Component{

    render() {
        return (
            <AboutPanel title=""
                        subTitle={<h4>, </h4>}
                        desc=", ; , " />
        );
    }

}

class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            input: ''
        }
    }


    handleInput = (e)=>{
        console.log(e.target.value);
        this.setState({
            input: e.target.value
        })
    }

    render() {
        return (
            <Layout>
                <Header>
                    <h2 style={{color: 'white'}}></h2>
                </Header>
                    <Content style={{backgroundColor: '#FFF'}}>
                        <Card title="" style={{marginTop: 10}}>
                            <p>xxx</p>
                        </Card>

                        <Card title="" style={{marginTop: 10}}>
                            <p>, </p>

                            <InputGroup>
                                <Input type="text" style={{width: '70%'}} value={this.state.input} onChange={this.handleInput}/>
                                <Button type='primary' onClick={()=> alert(this.state.input)}></Button>
                            </InputGroup>
                        </Card>


                        <Card title="" style={{marginTop: 10}}>
                            <p>, ; , </p>

                        </Card>
                    </Content>
            </Layout>
        );
    }
}

export default About;



